.catalog {
  padding: 40px 0 60px;
  .filter {
    display: none;
    justify-content: flex-end;
    margin-top: 10px;
    &-btn {
      display: flex;
      align-items: center;
      cursor: pointer;
      span {
        font-size: 18px;
        line-height: 130%;
      }
      i {
        flex-shrink: 0;
        width: 32px;
        display: flex;
        align-items: center;
        margin-left: 15px;
      }
      i img {
        width: 100%;
      }
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
  }
  &__content {
    width: calc(100% - 390px);
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    gap: 30px;
  }
  &-card {
    border: 1px solid #d4dde3;
    border-radius: 15px;
    padding: 15px 15px 30px;
    &__img {
      position: relative;
      padding-bottom: 66%;
    }
    &__img img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }
    &__desc {
      color: #232323;
      margin-top: 15px;
    }
    &__title {
      font-size: 26px;
      line-height: 120%;
      font-weight: 700;
    }
    &__subtitle {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      margin-top: 5px;
    }
    &__date {
      width: calc(100% + 30px);
      display: flex;
      align-items: center;
      background: #f4f6f7;
      padding: 12px 15px;
      margin-top: 15px;
      margin-left: -15px;
      i {
        flex-shrink: 0;
        width: 12px;
        margin-right: 10px;
      }
      i img {
        width: 100%;
      }
      span {
        font-size: 16px;
        line-height: 120%;
        font-weight: 500;
        color: #232323;
      }
    }
    ul {
      width: calc(100% + 30px);
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 10px;
      border-bottom: 1px solid #d4dde3;
      padding: 15px;
      margin-left: -15px;
      li {
        display: flex;
        align-items: center;
        font-size: 18px;
        line-height: 120%;
        i {
          flex-shrink: 0;
          width: 13px;
          height: 13px;
          display: flex;
          justify-content: center;
          align-items: center;
          background: #0085dd;
          border-radius: 50%;
          margin-right: 5px;
        }
        i img {
          width: 8px;
        }
      }
    }
    &__btn {
      width: 100%;
      height: 57px;
      margin-top: 15px;
      span {
        font-size: 20px;
      }
    }
  }
}
.sidebar {
  width: 350px;
  margin-right: 40px;
  &__inner {
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
    border: 1px solid #d4dde3;
    border-radius: 15px;
    padding: 30px;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    font-weight: 600;
    color: #3a3a3a;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 20px;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      position: relative;
      font-size: 21px;
      line-height: 120%;
      color: #3a3a3a;
      padding-left: 34px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 22px;
        height: 22px;
        border: 1px solid #8a9daa;
        border-radius: 5px;
      }
      &:before {
        content: '';
        position: absolute;
        top: 5px;
        left: 4px;
        width: 14px;
        height: 11px;
        background-image: url("../img/check2.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &__btns {
    width: calc(100% + 60px);
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 10px;
    border-top: 1px solid #d4dde3;
    padding: 20px 30px 0;
    margin-left: -30px;
  }
  &__btn,
  &__btn2 {
    width: 100%;
    height: 50px;
    span {
      font-size: 16px;
      text-transform: uppercase;
    }
  }
  &__btn {
    background: transparent;
    border: 2px solid #d4dde3;
    border-radius: 25px;
    &:hover {
      background: #e8e8e8;
    }
    span {
      font-weight: 700;
      color: #565656;
    }
  }
  &-bg {
    position: fixed;
    z-index: 14;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &--active {
      opacity: 1;
    }
  }
  &-close {
    position: fixed;
    top: 20px;
    right: 30px;
    width: 20px;
    cursor: pointer;
    opacity: 0;
    &--active {
      opacity: 1;
    }
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  &__btn {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #c2c2c2;
    border-radius: 50%;
    &:hover {
      background: #e8e8e8;
    }
    img {
      width: 14px;
    }
  }
  &__items {
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 0 40px;
  }
  &__item {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #c2c2c2;
    border-radius: 50%;
    &:hover {
      background: #0085dd;
      span {
        color: #ffffff;
      }
    }
    span {
      font-size: 24px;
      line-height: 105%;
      font-weight: 600;
      color: #0085dd;
    }
  }
  &__item--active {
    background: #0085dd;
    span {
      color: #ffffff;
    }
  }
}

@media screen and (max-width: 1300px) {
  .catalog {
    &__content {
      width: calc(100% - 290px);
    }
    &-card {
      padding: 10px;
      &__desc {
        margin-top: 10px;
      }
      &__title {
        font-size: 20px;
      }
      &__subtitle {
        font-size: 16px;
      }
      &__date {
        width: calc(100% + 20px);
        padding: 10px;
        margin-top: 10px;
        margin-left: -10px;
        i {
          width: 10px;
        }
        span {
          font-size: 14px;
        }
      }
      ul {
        width: calc(100% + 20px);
        padding: 10px;
        margin-left: -10px;
        li {
          font-size: 14px;
        }
      }
      &__btn {
        height: 42px;
        span {
          font-size: 16px;
        }
      }
    }
  }
  .sidebar {
    width: 260px;
    margin-right: 30px;
    &__inner {
      gap: 30px;
      padding: 20px;
    }
    &__subtitle {
      font-size: 18px;
    }
    &__item {
      label {
        font-size: 16px;
        &:after {
          top: -1px;
        }
        &:before {
          top: 4px;
        }
      }
    }
    &__btns {
      width: calc(100% + 40px);
      grid-template-columns: 100%;
      padding: 15px 20px 0;
      margin-left: -20px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .catalog {
    &__wrap {
      margin-top: 30px;
    }
  }
  .pagination {
    margin-top: 40px;
    &__btn {
      width: 45px;
      height: 45px;
      img {
        width: 11px;
      }
    }
    &__items {
      margin: 0 30px;
    }
    &__item {
      width: 45px;
      height: 45px;
      span {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 1160px) {
  .catalog {
    &__content {
      width: calc(100% - 250px);
    }
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
      gap: 24px;
    }
  }
  .sidebar {
    width: 220px;
  }
}

@media screen and (max-width: 992px) {
  .catalog {
    padding: 30px 0;
  }
  .pagination {
    margin-top: 30px;
    &__btn {
      width: 38px;
      height: 38px;
      img {
        width: 10px;
      }
    }
    &__items {
      gap: 5px;
      margin: 0 16px;
    }
    &__item {
      width: 38px;
      height: 38px;
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .catalog {
    .filter {
      display: flex;
    }
    &__wrap {
      position: relative;
      display: block;
    }
    &__content {
      width: 100%;
    }
    &-card {
      &__title {
        font-size: 18px;
      }
      &__subtitle {
        font-size: 16px;
      }
      &__date {
        span {
          font-size: 14px;
        }
      }
      ul {
        li {
          font-size: 14px;
        }
      }
    }
  }
  .sidebar {
    position: fixed;
    z-index: 15;
    top: 0;
    left: -105%;
    width: 300px;
    height: 100%;
    background: #ffffff;
    transition: all 0.5s;
    overflow-y: auto;
    &--active {
      left: 0;
    }
    &__inner {
      border: none;
      border-radius: 0;
    }
  }
}

@media screen and (max-width: 740px) {
  .catalog {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
    }
    &-card:nth-child(7) ~ .catalog-card {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
    }
    &-card {
      &__btn {
        height: 35px;
        margin-top: 10px;
        span {
          font-size: 14px;
        }
      }
    }
  }
  .sidebar {
    width: 100%;
    &__inner {
      padding-top: 60px;
    }
    &__btns {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
    &-close {
      position: absolute;
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .catalog {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 400px) {
  .sidebar {
    &__btns {
      grid-template-columns: 100%;
    }
  }
}