.tour {
  padding: 40px 0;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-top: 20px;
  }
  &__map {
    position: relative;
    width: 45%;
    .tour__location {
      display: none;
    }
  }
  .map2 {
    width: 100%;
    height: 100%;
    .ymaps-2-1-79-map {
      width: 100% !important;
      height: 100% !important;
    }
  }
  &__location {
    width: calc(45% - 40px);
    display: flex;
    align-items: center;
    margin-left: auto;
    i {
      flex-shrink: 0;
      width: 24px;
      margin-right: 12px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 21px;
      line-height: 130%;
      font-weight: 500;
      color: #232323;
    }
  }
}
.gallery {
  width: 55%;
  margin-right: 40px;
  &__big {
    .swiper-slide img {
      border-radius: 15px;
    }
  }
  &__small-wrapp {
    position: relative;
    padding: 0 50px;
    margin-top: 20px;
    .swiper-slide img {
      border-radius: 10px;
    }
  }
  .swiper-slide {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-image: linear-gradient(90.0deg, rgba(56,80,155,0.69) -24%, rgba(0,132,255,0.69) 125%);
      border-radius: 10px;
      opacity: 0;
    }
    img {
      width: 100%;
      display: block;
    }
  }
  .swiper-slide-thumb-active {
    &:after {
      opacity: 1;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    transform: translate(0, -50%);
    width: auto;
    height: auto;
    background: transparent;
    border: none;
    &:hover {
      background: transparent;
    }
  }
  .swiper-button-prev {
    left: 0;
  }
  .swiper-button-next {
    right: 0;
  }
}
.tour-info {
  background: #ebf7ff;
  border: 1px solid rgba(174,208,230,0.49);
  padding: 0 0 60px 0;
  .select__wrap {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 5000px;
      height: 1px;
      border-bottom: 1px solid rgba(174,208,230,0.49);
    }
  }
  .select__list-default {
    display: none;
  }
  .select__list {
    display: grid;
    grid-template-columns: repeat(4, calc(25%));
  }
  .tab {
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: 1px solid rgba(174,208,230,0.49);
    padding: 5px 15px;
    cursor: pointer;
    &:first-child {
      border-left: 1px solid rgba(174,208,230,0.49);
    }
    i {
      flex-shrink: 0;
      max-width: 24px;
      display: flex;
      align-items: center;
      margin-right: 12px;
    }
    i img {
      max-width: 100%;
    }
    span {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      color: #232323;
    }
  }
  .tab--active {
    background: #0085dd;
    span {
      color: #ffffff;
    }
  }
  &__content {
    margin-top: 60px;
  }
  .tabs__content {
    display: none;
  }
  .tabs__content--active {
    display: block;
  }
}

@media screen and (max-width: 1300px) {
  .tour-info {
    .tab {
      i {
        max-width: 18px;
        margin-right: 7px;
      }
      span {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .tour {
    &__location {
      width: calc(45% - 30px);
      i {
        width: 18px;
      }
      span {
        font-size: 16px;
      }
    }
  }
  .gallery {
    margin-right: 30px;
    &__small-wrapp {
      padding: 0 40px;
    }
  }
  .tour-info {
    .tab {
      height: 55px;
      span {
        font-size: 16px;
      }
    }
    &__content {
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .tour {
    padding: 30px 0;
  }
}

@media screen and (max-width: 960px) {
  .tour {
    &__wrap {
      margin-top: 16px;
    }
    &__map {
      width: 40%;
    }
    &__location {
      width: calc(40% - 30px);
      i {
        width: 14px;
        margin-right: 7px;
      }
      span {
        font-size: 12px;
      }
    }
  }
  .gallery {
    width: 60%;
    &__small-wrapp {
      padding: 0 30px;
    }
  }
  .tour-info {
    .tab {
      height: 50px;
      span {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 740px) {
  .tour {
    &__location {
      display: none;
    }
    &__wrap {
      display: block;
    }
    &__map {
      width: 100%;
      .tour__location {
        width: 100%;
        display: flex;
        justify-content: start;
        margin-bottom: 15px;
      }
    }
    .map2 {
      height: 400px;
    }
  }
  .gallery {
    width: 100%;
    margin: 0 0 20px 0;
    &__small-wrapp {
      padding: 0 20px;
    }
  }
  .tour-info {
    .select__wrap {
      max-width: 300px;
    }
    .select__wrap--active {
      .select__list {
        opacity: 1;
        pointer-events: all;
      }
    }
    .select__list-default {
      height: 45px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #0085dd;
      cursor: pointer;
    }
    .select__current {
      display: flex;
      align-items: center;
      i {
        flex-shrink: 0;
        max-width: 18px;
        display: flex;
        align-items: center;
        margin-right: 7px;
      }
      i img {
        max-width: 100%;
      }
      span {
        font-size: 14px;
        line-height: 120%;
        font-weight: 500;
        color: #ffffff;
      }
    }
    .select-arrow {
      flex-shrink: 0;
      width: 8px;
      display: flex;
      align-items: center;
      margin-left: 7px;
      img {
        width: 100%;
      }
    }
    .select__list {
      position: absolute;
      z-index: 1;
      top: calc(100% - 1px);
      left: 0;
      width: 100%;
      grid-template-columns: 100%;
      background: #ffffff;
      border: 1px solid rgba(174,208,230,0.49);
      border-top: unset;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    .tab {
      height: 45px;
      border-right: unset;
      &--active {
        width: calc(100% + 2px);
        margin-left: -1px;
      }
      &:first-child {
        border-left: unset;
      }
      &:not(:first-child) {
        border-bottom: 1px solid rgba(174,208,230,0.49);
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .tour {
    .map2 {
      height: 200px;
    }
  }
}