.tour-main {
  padding: 40px 0 60px;
  &__flex {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px 30px;
    margin-top: 20px;
  }
  &__subtitle {
    font-size: 30px;
    line-height: 130%;
    font-weight: 600;
    color: #282828;
  }
  &__doc {
    height: 48px;
    display: flex;
    align-items: center;
    background: #ebf7ff;
    border-radius: 18px;
    padding: 3px 20px;
    &:hover {
      background: #d3efff;
    }
    i {
      flex-shrink: 0;
      width: 20px;
      margin-right: 12px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      color: #556d7d;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 20px;
    p {
      font-size: 21.5px;
      line-height: 130%;
      color: #282828;
    }
  }
  &__tabs {
    margin-top: 30px;
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 40px;
  }
  &__tab {
    max-width: 355px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px;
    border: 1px solid #d4dde3;
    border-radius: 10px;
    padding: 20px 30px;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background: #0085dd;
      p:nth-child(1) {
        &:after {
          background-image: url("../img/calendar.webp");
        }
      }
      p {
        color: #ffffff;
      }
    }
    p:nth-child(1) {
      position: relative;
      font-weight: 700;
      padding-left: 30px;
      &:after {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        width: 18px;
        height: 18px;
        background-image: url("../img/calendar2.webp");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
    p {
      font-size: 21.5px;
      line-height: 130%;
      color: #232323;
    }
  }
  .tab--active {
    background: #0085dd;
    p:nth-child(1) {
      &:after {
        background-image: url("../img/calendar.webp");
      }
    }
    p {
      color: #ffffff;
    }
  }
  &__content {
    margin-top: 30px;
  }
  .tabs__content {
    display: none;
  }
  .tabs__content--active {
    display: block;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
  }
  &__item {
    border: 1px solid #d4dde3;
    border-radius: 15px;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #eff6fb;
      border-radius: 15px 15px 0 0;
      padding: 20px 40px;
      cursor: pointer;
    }
    &-head {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 10px;
    }
    &-day {
      display: flex;
      align-items: center;
      font-size: 32.2px;
      line-height: 120%;
      font-weight: 600;
      color: #0085dd;
      span {
        flex-shrink: 0;
        width: 64px;
        height: 64px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #0085dd;
        border-radius: 50%;
        color: #ffffff;
        margin-right: 12px;
      }
    }
    &-date {
      position: relative;
      font-size: 21.5px;
      line-height: 120%;
      font-weight: 500;
      color: #232323;
      padding-left: 28px;
      &:after {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        width: 18px;
        height: 18px;
        background-image: url("../img/calendar2.webp");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
    &-btn {
      width: 10px;
      margin-left: 32px;
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    &-hidden {
      display: none;
    }
    &-subtitle {
      font-size: 26px;
      line-height: 130%;
      font-weight: 600;
      color: #282828;
    }
  }
  .show--active {
    .tour-main__item-btn {
      transform: rotate(90deg);
    }
  }
  &__group {
    padding: 40px;
    &:not(:last-child) {
      border-bottom: 2px solid #d4dde3;
    }
  }
  &__info {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
  }
  &__info-item {
    display: flex;
  }
  &__info-time {
    flex-shrink: 0;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #0085dd;
    border-radius: 14px;
    font-size: 24px;
    line-height: 105%;
    font-weight: 700;
    color: #ffffff;
    padding: 0 14px;
    margin-right: 14px;
  }
  &__info-text {
    font-size: 21.5px;
    line-height: 130%;
    color: #282828;
    margin-top: 3px;
  }
  .orange {
    .tour-main__info-time {
      background: #ff3c00;
    }
    .tour-main__info-text {
      color: #ff3c00;
    }
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-top: 20px;
    li {
      position: relative;
      display: flex;
      font-size: 21.5px;
      line-height: 130%;
      font-weight: 600;
      color: #282828;
      i {
        flex-shrink: 0;
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #ed6c1c;
        background-image: linear-gradient(90.0deg, #38509b -23%, #0084ff 126%);
        border-radius: 50%;
        margin-right: 20px;
      }
      i img {
        width: 16px;
      }
    }
  }
}
.info {
  color: #282828;
  background: #ebf7ff;
  padding: 60px 0;
}
.connection {
  background: #f5fbff;
  padding: 60px 0;
  .form {
    margin-top: 30px;
  }
  .form-flex {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 20px / 3));
    gap: 20px;
  }
  .form-group {
    position: relative;
    input {
      width: 100%;
      height: 68px;
      font-size: 20.1px;
      line-height: 120%;
      font-weight: 400;
      color: #282828;
      padding: 0 16px 0 60px;
      &::placeholder {
        color: #9b9b9b;
      }
    }
    textarea {
      width: 100%;
      height: 205px;
      font-size: 20.1px;
      line-height: 120%;
      font-weight: 400;
      color: #282828;
      padding: 30px 16px 30px 60px;
      &::placeholder {
        color: #9b9b9b;
      }
    }
    textarea ~ img {
      top: 36px;
      transform: unset;
    }
    img {
      position: absolute;
      top: 50%;
      left: 24px;
      transform: translate(0, -50%);
      max-width: 16px;
    }
  }
  .form-group--100 {
    grid-column: 1/4;
  }
  .form-bottom {
    display: flex;
    align-items: center;
    margin-top: 30px;
  }
  .form-btn {
    width: 394px;
    height: 77px;
    span {
      font-size: 26px;
      text-transform: uppercase;
    }
  }
  .form-check {
    margin-left: 30px;
  }
}
.services2 {
  padding: 60px 0 120px;
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    height: auto;
  }
  .swiper-button-next {
    transform: translate(0, -50%);
    background: #ffffff;
    right: -90px;
  }
  .swiper-button-prev {
    transform: translate(0, -50%);
    background: #ffffff;
    left: -90px;
  }
  .swiper-pagination {
    bottom: -40px;
    &-bullet {
      border-color: #82bce5;
    }
  }
}

@media screen and (max-width: 1750px) {
  .services2 {
    .swiper-button-prev {
      left: -25px;
    }
    .swiper-button-next {
      right: -25px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .tour-main {
    &__btns {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .tour-main {
    &__flex {
      gap: 10px 20px;
      margin-top: 16px;
    }
    &__subtitle {
      font-size: 22px;
    }
    &__doc {
      height: 30px;
      i {
        width: 15px;
      }
      span {
        font-size: 14px;
      }
    }
    &__text {
      margin-top: 16px;
      p {
        font-size: 16px;
      }
    }
    &__tabs {
      margin-top: 20px;
    }
    &__tab {
      max-width: 265px;
      gap: 5px;
      padding: 15px 25px;
      p:nth-child(1) {
        padding-left: 25px;
        &:after {
          top: 0;
          width: 15px;
          height: 15px;
        }
      }
      p {
        font-size: 16px;
      }
    }
    &__content {
      margin-top: 20px;
    }
    &__items {
      gap: 16px;
    }
    &__item {
      &-show {
        padding: 15px 30px;
      }
      &-day {
        font-size: 24px;
        span {
          width: 45px;
          height: 45px;
          margin-right: 8px;
        }
      }
      &-date {
        font-size: 16px;
        padding-left: 25px;
        &:after {
          top: 0;
          width: 15px;
          height: 15px;
        }
      }
      &-subtitle {
        font-size: 18px;
      }
    }
    &__group {
      padding: 30px;
    }
    &__info-time {
      height: 28px;
      font-size: 18px;
      padding: 0 10px;
    }
    &__info-text {
      font-size: 16px;
    }
    ul {
      li {
        font-size: 16px;
        i {
          width: 25px;
          height: 25px;
          margin-right: 12px;
        }
      }
    }
  }
  .info {
    padding: 40px 0;
  }
  .connection {
    padding: 40px 0;
    .form-group {
      img {
        left: 16px;
      }
      input {
        height: 50px;
        font-size: 16px;
        padding:  0 16px 0 40px;
      }
      textarea {
        height: 150px;
        font-size: 16px;
        padding: 20px 16px 20px 40px;
        & ~ img {
          top: 24px;
        }
      }
    }
    .form-bottom {
      margin-top: 20px;
    }
    .form-btn {
      width: 294px;
      height: 58px;
      span {
        font-size: 20px;
      }
    }
  }
  .services2 {
    padding: 40px 0 80px;
    &__slider {
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .tour-main {
    padding: 30px 0;
    &__subtitle {
      font-size: 18px;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__btns {
      gap: 24px;
    }
    &__item {
      &-show {
        padding: 15px;
      }
      &-day {
        font-size: 18px;
        span {
          width: 40px;
          height: 40px;
        }
      }
      &-date {
        font-size: 14px;
      }
      &-btn {
        width: 8px;
      }
      &-subtitle {
        font-size: 16px;
      }
    }
    &__group {
      padding: 25px 15px;
    }
    &__info-time {
      font-size: 16px;
      margin-right: 8px;
    }
    &__info-text {
      font-size: 14px;
    }
    ul {
      li {
        font-size: 14px;
        i {
          width: 20px;
          height: 20px;
        }
        i img {
          width: 12px;
        }
      }
    }
  }
  .info {
    padding: 30px 0;
  }
  .connection {
    padding: 30px 0;
    .form-flex {
      grid-template-columns: repeat(3, calc(100% / 3 - 30px / 3));
      gap: 15px;
    }
    .form-group {
      input {
        height: 45px;
        font-size: 14px;
      }
      textarea {
        height: 130px;
        font-size: 14px;
        & ~ img {
          top: 21px;
        }
      }
    }
    .form-bottom {
      margin-top: 20px;
    }
    .form-btn {
      width: 254px;
      height: 50px;
      span {
        font-size: 16px;
      }
    }
  }
  .services2 {
    padding: 30px 0 60px;
  }
}

@media screen and (max-width: 960px) {
  .tour-main {
    &__btns {
      gap: 16px;
    }
    &__tab {
      max-width: 240px;
      padding: 15px;
    }
  }
  .services2 {
    .swiper-button-prev {
      left: -15px;
    }
    .swiper-button-next {
      right: -15px;
    }
  }
}

@media screen and (max-width: 740px) {
  .connection {
    .form-flex {
      grid-template-columns: 100%;
    }
    .form-group--100 {
      grid-column: 1;
    }
    .form-bottom {
      flex-direction: column;
    }
    .form-check {
      margin: 16px 0 0 0;
    }
  }
  .services2 {
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .tour-main {
    &__btns {
      grid-template-columns: 100%;
    }
    &__tab {
      max-width: 100%;
    }
  }
  .connection {
    .form-btn {
      width: 224px;
      height: 44px;
      span {
        font-size: 14px;
      }
    }
  }
}